.transition, .button-big, .button, .button-big--arrow, .button-big.icon::after, .button-big--quaternary::after, .button-big--tertiary::after, .button-big--secondary::after, .button-big--primary::after {
  transition: all 0.3s;
}

.button-big--arrow, .button-big.icon::after, .button-big--quaternary::after, .button-big--tertiary::after, .button-big--secondary::after, .button-big--primary::after {
  content: "";
  height: 1.875rem;
  width: 1.875rem;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 0.9375rem;
  right: 1.25rem;
}
@media (hover: hover) {
  .button-big--arrow:hover, .button-big.icon:hover::after, .button-big--quaternary:hover::after, .button-big--tertiary:hover::after, .button-big--secondary:hover::after, .button-big--primary:hover::after {
