/*! Huebee v1.0.1
http://huebee.buzz
---------------------------------------------- */
.huebee {
  position: absolute;
  z-index: 1;
  transform: translateY(0);
  transition: opacity 0.15s, transform 0.15s;
}

.huebee.is-hidden {
  opacity: 0;
  transform: translateY(10px);
}

.huebee.is-static-open {
  position: relative;
  z-index: auto;
}

.huebee__container {
  position: absolute;
  left: 0;
  top: 5px;
  padding: 10px;
  background: #EEE;
  border-radius: 5px;
  box-shadow: 0 5px 10px hsla(0, 0%, 0%, 0.3);
}

.huebee.is-static-open .huebee__container {
  position: relative;
  display: inline-block;
  left: auto;
  top: auto;
  box-shadow: none;
}

.huebee__canvas {
  display: block;
  cursor: pointer;
}

.huebee__cursor {
  width: 15px;
  height: 15px;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: content-box;
  border: 3px solid #fff;
  border-radius: 5px;
  pointer-events: none;
}

.huebee__cursor.is-hidden {
  opacity: 0;
}

.huebee__close-button {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  top: -9px;
  right: -9px;
  border-radius: 12px;
  background: #222;
}

.huebee__close-button__x {
  stroke: #fff;
  stroke-width: 3;
  stroke-linecap: round;
}

.huebee__close-button:hover {
  background: #fff;
  cursor: pointer;
}

.huebee__close-button:hover .huebee__close-button__x {
  stroke: #222;
}

/*************************************
***** New Start Page 2024 ***** 
*************************************/
/*************************************
***** Typography ***** 
*************************************/
/*************************************
***** Font Sizes *****
*************************************/
.font-size--xs {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.font-size--s, p {
  font-size: 1rem;
  line-height: 1.5rem;
}

.font-size--md, .teaser-news--header, p.teaser, h5, h4 {
  font-size: 1.25rem;
  line-height: 1.875rem;
}

.font-size--lg, h3, h2, h1 {
  font-size: 1.875rem;
  line-height: 2.5rem;
}

/*************************************
***** Headlines *****
*************************************/
h1, h2, h3, h4, h5 {
  font-weight: 700;
}

h1 {
  margin-bottom: 1.875rem;
}

h2 {
  margin-bottom: 1.25rem;
}

h3 {
  margin-bottom: 1.875rem;
}

.tint {
  display: inline-block;
  padding: 0.625rem 1.25rem;
  margin: 0 -0.625rem 0 -0.625rem;
  mix-blend-mode: multiply;
  color: #fff;
}
.tint--primary {
  background: #39378B;
  transform: rotate(1deg);
  position: relative;
  top: 0.3125rem;
}
.tint--secondary {
  background: #FDC300;
  transform: rotate(-1deg);
}
.tint--tertiary {
  background: #33B8CA;
  transform: rotate(-2deg);
}
.tint--quaternary {
  background: #88BCE2;
  transform: rotate(1deg);
  position: relative;
  top: -0.5rem;
}

/*************************************
***** Paragraph & Lists *****
*************************************/
p {
  margin-bottom: 0.9375rem;
}
p:last-child {
  margin-bottom: 0;
}
p.teaser:last-child {
  margin-bottom: 0.9375rem;
}
p.teaser + .button {
  margin-top: 1.25rem;
}

/*************************************
***** Links *****
*************************************/
a {
  color: #39378B;
  text-decoration: underline;
}
@media (hover: hover) {
  a:hover {
    cursor: pointer;
    text-decoration: none;
  }
}

/*************************************
***** Buttons *****
*************************************/
.button {
  display: inline-block;
  border-radius: 0.3125rem;
  text-decoration: none;
  font-weight: 700;
  padding: 0.9375rem;
}
.button--light {
  color: #39378B;
