/*************************************
***** Sections ***** 
*************************************/
section {
  width: 100%;
}

.section--white {
  background: #fff;
}
.section--light {
  background: #e9ecef;
}

#start {
  background: #fff;
}

/*************************************
***** Banner ***** 
*************************************/
#startup-message-statustext {
  background: #39378B;
  color: #fff;
  position: absolute;
  z-index: 999;
  bottom: 3.125rem;
  right: 3.125rem;
  width: 100%;
  max-width: 28.125rem;
  border-radius: 0.3125rem;
  box-shadow: 0 0 1.25rem rgba(0, 0, 0, 0.25);
}
#startup-message-statustext h4, #startup-message-statustext p {
  padding: 1.25rem;
}

/*************************************
***** NEPO Block in Colors ***** 
*************************************/
.nepo {
  display: inline-block;
}
.nepo::before {
  content: "";
  display: inline-block;
  width: 5.3125rem;
  height: 1.25rem;
  background-image: url("img/nepo.svg");
  background-position: left bottom;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 0.3125rem;
}

/*************************************
***** News ***** 
*************************************/
.teaser-news {
  display: flex;
  background: #e9ecef;
  padding: 0.9375rem;
  border-radius: 0.3125rem;
}
@media (min-width: 992px) {
  .teaser-news {
    padding: 2.5rem;
  }
}
.teaser-news--header {
  font-weight: 700;
  margin-bottom: 0.625rem;
}
.teaser-news--body h3 {
  color: #39378B;
}
.teaser-news--image {
  width: 100%;
  height: auto;
}

/*************************************
***** Footer ***** 
*************************************/
#start footer {
  border-top: 0.0625rem solid rgba(0, 0, 0, 0.2);
  padding-bottom: 6rem;
  width: 100%;
}
#start footer img {
  width: 100%;
}
#start footer p,
#start footer .nav-link {
  font-size: 0.875rem;
  line-height: 1.25rem;
  margin-right: 0;
}
#start footer .nav-item {
  background: none;
}
#start footer .nav-item:hover {
  background: none;
}
#start footer .nav-link {
  padding: 0.3125rem 0;
  color: #6c757d;
}

/*************************************
***** Safari Flexbox Fix for 100% Height *****
*************************************/
@supports (background: -webkit-named-image(i)) {
  .flexbox-fix {
    display: flex;
  }
}